export default {
  name: "InvitedCard",
  props: {
    showInvitedlist: {
      type: Boolean,
      default: false
    },
    inviteAddress: {
      type: Array,
      default: () => []
    },
    inviteNum: {
      type: Number,
      default: 0
    },
    luckRate: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      visables: this.showInvitedlist,
    }
  },
  watch: {
    showInvitedlist(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeInvitedCard", false);
    }
  }
}

